<template>
  <div class="image-table">
    <h1>镜像列表</h1>
    <div class="image-btn">
      <button class="btn-new"
              @click="handleNewImage">
        <i class="iconfont icon-hao  btn-new-icon">&#xe66e;
        </i>
        新建镜像
      </button>
      <!--<button class="btn-delete">-->
      <!--<i class="iconfont icon-delete ">&#xe663;-->
      <!--</i>-->
      <!--</button>-->
    </div>
    <div class="ibox-content">
      <table>
        <thead>
        <tr>
          <th>存储库</th>
          <th>注册表/类型</th>
          <th>安全问题</th>
          <th>IMAGE创建时间</th>
          <th>SIZE
            <i class="iconfont icon-descending">&#xe624;</i>
          </th>
          <th>
            操作
          </th>
        </tr>
        </thead>
        <tbody v-if="isRepo==''">
        <tr v-for="item in net" :key="item.index">
          <td>
            <router-link
              :to="{ path: 'overview', query: { repotag: item.repotag }}">
              {{item.repotag}}
            </router-link>
          </td>
          <td>
            <i class="iconfont icon-docker index-warning"> &#xe672;</i>
            官方
          </td>
          <td>
            <i class="iconfont icon-system-fn-warning  index-warning icon-red">
              &#xe638;</i>
            <span>{{item.high}}</span>
            <i class="iconfont icon-system-fn-warning index-warning icon-orange">
              &#xe638;</i>
            <span>{{item.low}}</span>
            <i class="iconfont icon-system-fn-warning index-warning icon-yellow">
              &#xe638;</i>
            <span>{{item.medium}}</span>
            <i class="  icon-system-fn-warning index-warning icon-blue">
              &#xe638;</i>
            <span>{{item.negligible}}</span>
          </td>
          <td>{{(new Date(item.createdTime * 1000)).toLocaleString()}}</td>
          <td v-if="(Math.round(item.size / 1024/1024))<1024">
            {{Math.round(item.size / 1024/1024)}}MB
          </td>
          <td v-else>{{(item.size / 1024/1024/1024).toFixed(1)}}GB</td>
          <td>
            <a @click="handleDeleteImage(item.repotag)">删除</a>
          </td>
        </tr>
        </tbody>
        <tbody v-else v-for="query in isRepo" :key="query.index">
        <tr v-if="query==item.repotag" v-for="item in net" :key="item.index">
          <td>
            <router-link
              :to="{ path: 'overview', query: { repotag: item.repotag }}">
              {{item.repotag}}
            </router-link>
          </td>
          <td>
            <i class="iconfont icon-docker index-warning"> &#xe672;</i>
            官方
          </td>
          <td>
            <i class="iconfont icon-system-fn-warning  index-warning icon-red">
              &#xe638;</i>
            <span>{{item.high}}</span>
            <i class="iconfont icon-system-fn-warning index-warning icon-orange">
              &#xe638;</i>
            <span>{{item.low}}</span>
            <i class="iconfont icon-system-fn-warning index-warning icon-yellow">
              &#xe638;</i>
            <span>{{item.medium}}</span>
            <i class="  icon-system-fn-warning index-warning icon-blue">
              &#xe638;</i>
            <span>{{item.negligible}}</span>
          </td>
          <td>{{(new Date(item.createdTime * 1000)).toLocaleString()}}</td>
          <td v-if="(Math.round(item.size / 1024/1024))<1024">
            {{Math.round(item.size / 1024/1024)}}MB
          </td>
          <td v-else>{{(item.size / 1024/1024/1024).toFixed(1)}}GB</td>
          <td>
            <a href="#" @click="handleDeleteImage(item.repotag)">删除</a>
          </td>
        </tr>
        </tbody>
      </table>
      <div class="divblock">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="listQuery.page"
          :page-sizes="[10, 20, 40, 80]"
          :page-size.sync="listQuery.limit"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import { fetchList, fetchImage, pullImage, deleteImage } from '@/api/transaction'

  export default {
    props: ['isRepo'],
    data() {
      return {
        storageList: [],
        listQuery: {
          limit: 10,
          page: 1
        },
        fullscreenLoading: false,
        num: 0,
        pullImageMessage: ''
      }
    },
    computed: {
      net() {
        if (this.storageList.length > this.listQuery.limit) {
          return this.storageList.slice((
            this.listQuery.page - 1) * this.listQuery.limit,
            this.listQuery.limit * this.listQuery.page)
        }
        return this.storageList
      },
      total() {
        return this.storageList.length
      }
    },
    methods: {
      getStorageList() {
        fetchList().then(res => {
          res.data.Mirror_list.forEach(item => {
            if (item.repotags !== null) {
              const url = item.repotags
              // console.log(url)
              if (url === '<none>:<none>') {
                this.num = this.num + 1
              } else {
                fetchImage(url).then(response => {
                  this.storageList.push({
                    repotag: item.repotags,
                    high: response.data.level.High,
                    medium: response.data.level.Medium,
                    low: response.data.level.Low,
                    negligible: response.data.level.Negligible,
                    createdTime: response.data.dockerImage[0].Created,
                    size: item.size
                  })
                })
              }
            }
          })
          this.$message({
            message: '提示：扫描到' + this.num + '个镜像tag为空，请及时处理。',
            type: 'warning',
            showClose: 'true',
            duration: 10000
          })
        })
      },
      handleSizeChange(val) {
        this.listQuery.limit = val
      },
      handleCurrentChange(val) {
        this.listQuery.page = val
      },
      handleNewImage() {
        this.$prompt('请输入需要添加的镜像(例如：(image:tag))，确定添加请按确定按钮。有些镜像特别大，所以会出现刷新后没有出现镜像，但是镜像库是存在下载的镜像，您需要多刷新几次，就能找到添加的镜像。', '提示', {
          confirmButtonText: '确定',
          cancelButtonClass: '取消'
        }).then(({ value }) => {
          this.fullscreenLoading = true
          pullImage(value).then(res => {
            this.pullImageMessage = res.data
            if (res.data.cod === -1) {
              this.$message({
                type: 'error',
                message: res.data.pill,
                duration: 10000
              })
            } else {
              const loading = this.$loading({
                text: '正在下载，请勿离开',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
              })
              setTimeout(() => {
                loading.close()
                // location.reload()
                this.$alert(this.pullImageMessage, '下载过程', {
                  confirmButtonText: '确定',
                  // eslint-disable-next-line
                  callback: action => {
                    location.reload()
                    this.$message({
                      type: 'info',
                      // eslint-disable-next-line
                      message: '数据已加载'
                    })
                  },
                  lockScroll: false,
                  customClass: 'alertMessage',
                  type: 'success'
                })
              }, 120000)
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          })
        })
      },
      handleDeleteImage(query) {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          deleteImage(query).then(res => {
            if (res.data.cod === 0) {
              location.reload()
            } else {
              this.$message({
                type: 'info',
                message: '删除失败'
              })
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      }
    },
    mounted() {
      this.getStorageList()
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .image-table
    width: 93%
    margin: 0 auto

  .image-btn
    display: inline-block
    float: right
    padding: 0

  button
    margin: 45px 0px 0 15px

  .btn-new
    text-align: center
    color: #FFFFFF
    foot-size: 16px
    background: #3E7AF6
    width: 100px
    height: 34px
    padding: 0 0 2px 0

  .btn-new:hover
    background: #2360DB

  /*.btn-delete*/
  /*float: clear*/
  /*height: 34px*/
  /*background: #EDEDED*/

  /*.btn-delete:hover*/
  /*background: #E0E0E0*/

  .ibox-content
    text-align: center
    margin: 0 auto

  .image-table
    width: 93%
    margin: 0 auto

  .ibox-content
    text-align: center
    margin: 0 auto

  .image-table h1
    margin: 58px 0 24px 50px

  .ibox-content table
    width: 100%
    max-width: 100%
    border-collapse: collapse
    margin-bottom: 40px

  .ibox-content table thead tr
    background: #F7F7F7
    height: 50px
    box-shadow: 4px 4px 20px #FAFAFA

  .ibox-content table thead tr th
    font-size: 1.5em
    font-weight: bold
    color: #525252

  .ibox-content table tbody tr
    font-size: 1.4em
    color: #525252

  .ibox-content table tbody tr td span
    display: inline-block
    width: 20px

  .ibox-content table tbody tr td:first-child
    width: 5%

  .ibox-content table tbody tr td:last-child,
  .ibox-content table thead tr th:last-child
    width: 10%

  .ibox-content table tbody tr td span
    display: inline-block
    width: 20px

  .ibox-content table tbody tr td:first-child
    width: 20%

  .ibox-content table tbody tr td:last-child,
  .ibox-content table thead tr th:last-child
    width: 10%

  tr
    height: 50px

  .ibox-content table tbody tr td span
    margin: 0 9px 0 0

  tr:hover
    background: #FAFAFA

  .divblock
    float: right

  .lable
    font-size: 0.9em
    color: #999
    position: relative
    top: 14px
    display: inline-block
    padding: 6px 8px
    background: #E6E6E6

  .index-warning
    font-size: 1.4em

  tr:hover
    background: #FAFAFA

  .lable
    font-size: 0.9em
    color: #999
    position: relative
    top: 14px
    display: inline-block
    padding: 6px 8px
    background: #E6E6E6

  .divblock
    float: right
    margin-bottom 60px

  .icon-delete
    color: #3E7AF6
    font-size: 1.5em

  .icon-descending
    color: #808080

  .icon-docker
    color: #3E7AF6

  .icon-red
    color: #FF0000

  .icon-orange
    color: #F27935

  .icon-yellow
    color: #F9BF3B

  .icon-blue
    color: #08B1D5
</style>
<style lang="stylus">
  .alertMessage
    width 1000px
    height 500px
    overflow-y auto
</style>



