<template>
  <div class="search-wrapper">
    <p class="search-box">
      <i class="iconfont icon-search">&#xe665;</i>
      <span class="split-line">|</span>
      <input class="search-text clear-edge" type="text"
             placeholder="搜索镜像（nginx,mysql,ubutu..." v-model="content">
      <button class="search-btn clear-edge" @click="search">搜索</button>
    </p>
    <!--<p class="search-record">-->
    <!--<strong>最近访问：</strong>-->
    <!--<span v-for="record in records"
    @click="handleFill(record)" :key="record">{{record}}</span>-->
    <!--<span v-if="showEllipsis">...</span>-->
    <!--</p>-->
  </div>
</template>

<script type="text/ecmascript-6">
  import { searchImage } from '@/api/transaction'

  export default {
    name: 'search',
    data() {
      return {
        content: '',
        records: ['liarbry / nginx', 'mysql / lidabno', 'ubutu / dh0qwh'],
        showEllipsis: false,
        list: []
      }
    },
    methods: {
      search() {
        const val = this.content
        searchImage(val).then(res => {
          res.data.forEach(item => {
            this.list.push(item.repotags)
          })
          this.$emit('isRepotags', this.list)
          this.list = []
        })
      }
      // handleFill(data) {
      //   this.content = data
      // }
    },
    mounted() {
      window.addEventListener('keyup', (event) => {
        if (event.keyCode === 13) {
          this.search()
        }
      })
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  i[id^="icon"]
    background: url(../img/icon.png)
    display: inline-block

  .search-wrapper
    margin: 0 auto
    width: 63.5%
    position: relative
    top: 50%
    .search-box
      height: 55px
      line-height: 55px
      background: #2D3C54
      border-radius: 5px
      -moz-border-radius: 5px /* Old Firefox */
      transition: 0.7s
      padding: 0 0 0 15px
      &:hover
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .6)
      #icon-search
        background-position: -0px -35px
        width: 22px
        height: 22px
        margin: 0 10px 0 16px
      .split-line
        color: #818796
        font-size: 2.0em
        margin: 0 13px 0 0
      .search-text
        color: #CCCCCC
        background: #2D3C54
        width: 83%
        font-size: 2em
        display: inline-block
        vertical-align: middle
      .search-btn
        cursor: pointer
        font-size: 1.4em
        color: #E6E6E6
        line-height: 32px
        padding: 0px 34px
        background: #3F5067
        border-radius: 4px
        -moz-border-radius: 4px /* Old Firefox */
        &:hover
          background: #3F5066
    .search-record
      font-size: 1.4em
      color: #ccc
      line-height: 38px
      span
        margin-right: 15px

  .icon-search
    color: #CCCCCC
    font-size: 2.4em
</style>



