<template>
  <div class="main">
    <navbar>
      <!--<span class="logo-text">DeepDefense容器安全监测平台</span>-->
    </navbar>
    <div class="search">
      <search @isRepotags="lisRepotags"></search>
    </div>
    <section class="table-wrapper">
      <img-table :isRepo="repo"></img-table>
    </section>
  </div>
</template>

<script type="text/ecmascript-6">
  import Navbar from 'components/navbar'
  import Search from './components/search'
  import ImgTable from './components/imgTable'

  export default {
    data() {
      return {
        repo: ''
      }
    },
    name: 'index',
    components: {
      Navbar,
      Search,
      ImgTable
    },
    methods: {
      lisRepotags(data) {
        if (data !== '') {
          this.repo = data
        } else {
          location.reload()
        }
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .search
    margin: 0 auto
    width: 100%
    height: 450px
    background: url(./img/bg.jpg) center
    background-size: cover
    overflow: hidden
</style>



